<template>
  <div class="calculator-page" v-if="calculator">
    <v-toolbar dense :dark="true">
      <v-app-bar-nav-icon @click="goToHome"></v-app-bar-nav-icon>

      <v-spacer></v-spacer>

      <v-toolbar-title>{{ calculator.name }}</v-toolbar-title>

      <v-spacer></v-spacer>
      <v-app-bar-nav-icon @click="refreshCalculator">
        <template>
          <v-icon>mdi-refresh</v-icon>
        </template>
      </v-app-bar-nav-icon>
    </v-toolbar>

    <v-main>
      <v-row class="justify-start px-3 mt-3">
        <v-col
          cols="12"
          :class="{ 'py-0': !showAdvanced && input.advanced, 'py-1': !input.advanced || showAdvanced }"
          v-for="(input, index) in calculator.inputs"
          :key="index"
        >
          <calc-input
            v-if="!input.advanced || showAdvanced"
            :input="input"
            :locked="lockIndex == index"
            :saved="saved.includes(index)"
            :calculated="lastUpdatedInputs.includes(index)"
            :currency="currency ? currency.symbol :'$'"
            @on-change="onChange"
            @on-focus="onFocus(index)"
            @on-blur="onBlur(index)"
            @on-lock="onLock(index)"
            @on-save="onSave(index)"
          />
        </v-col>

        <div
          v-if="hasAdvanced.length"
          cols="6"
          class="px-3 ma-3"
          style="background-color: rgba(255, 255, 255, 0.4)"
        >
          <v-switch
            v-model="showAdvanced"
            hide-details
            color="#44b3e0"
            dark
            class="ma-2"
            label="Show advanced Mode"
          ></v-switch>
        </div>
      </v-row>

      <center class="ma-4">
        <p class="text small white--text">{{ calculator.description }}</p>
      </center>
    </v-main>
  </div>
</template>

<script>
import { mapState } from "vuex";
import CalcInput from "@/components/CalcInput";

export default {
  name: "Calculator",

  props: ["name"],

  components: { CalcInput },

  data: () => {
    return {
      initCalculator: null,
      calculator: {},
      showAdvanced: false,

      blurIndex: null,
      focusIndex: null,
      lockIndex: null,

      lastUpdatedInputs: [],
      lastUpdatedAdvancedInput: null, // null for calculator without advanced input
      twoAdvancedParameter: null, // when change calc with two parameters of advanced inputs
      initializedAdvanced: null,
      hasAdvanced: [],
      saved: []
    };
  },

  methods: {
    goToHome() {
      this.$router.replace({ name: "Home" });
    },

    refreshCalculator() {
      if (!this.initCalculator) {
        this.initCalculator = this.$store.getters["calculatorWithName"](
          this.name
        );
      }

      this.calculator = JSON.parse(this.initCalculator);

      if (!this.calculator) {
        this.$router.replace({ name: "Home" });
      }

      this.calculator.inputs.map((inp, index) => {
        if (inp.advanced) {
          this.hasAdvanced.push(index);
          if (inp.value !== "") {
            this.initializedAdvanced = index;
          }
        }
        if (inp.loaded) {
          this.saved.push(index);
          this.lastUpdatedInputs.push(index);
        }
      });

      this.onChange();
    },

    onChange() {
      const updates = [];

      if (this.focusIndex !== null) {
        updates.push(this.focusIndex);
      }

      if (this.lockIndex !== null) {
        updates.push(this.lockIndex);
      }

      if (this.blurIndex !== null && updates.length < 2) {
        updates.push(this.blurIndex);
      }

      if (this.saved.length && updates.length < 2) {
        this.saved.map(i => updates.push(i));
      }

      if (
        this.lastUpdatedAdvancedInput !== null &&
        !updates.includes(this.lastUpdatedAdvancedInput) &&
        updates.length < 3
      ) {
        updates.push(this.lastUpdatedAdvancedInput);
      }

      this.lastUpdatedInputs = [];

      const res = this.updateInputs(0, updates);
      console.log(res);
    },

    onFocus(index) {
      if (this.hasAdvanced.includes(index)) {
        this.twoAdvancedParameter =
          this.twoAdvancedParameter === null ? false : true;

        if (this.twoAdvancedParameter) {
          this.blurIndex = this.focusIndex;
          this.focusIndex = this.lastUpdatedAdvancedInput;
        }

        this.lastUpdatedAdvancedInput = index;
      } else {
        if (this.twoAdvancedParameter === true) {
          this.twoAdvancedParameter = false;
          this.blurIndex = this.focusIndex;
        } else if (this.twoAdvancedParameter === false) {
          this.twoAdvancedParameter = null;
          this.lastUpdatedAdvancedInput = this.focusIndex;
          if (this.lastUpdatedAdvancedInput == index) {
            this.lastUpdatedAdvancedInput = this.blurIndex;
          }
        } else {
          this.lastUpdatedAdvancedInput = this.initializedAdvanced;
        }
        this.focusIndex = index;
      }
    },

    onBlur(index) {
      setTimeout(() => {
        if (this.focusIndex == index || this.twoAdvancedParameter === false) {
          return;
        }

        this.blurIndex = index;

        if (this.lockIndex !== null) {
          this.blurIndex = this.lockIndex;
        }
      }, 10);
    },

    onLock(index) {
      if (this.lockIndex == index) {
        this.lockIndex = null;
      } else if (this.lockIndex === null) {
        this.lockIndex = index;
      }
    },

    onSave(input) {
      const key = this.calculator.inputs[input].id;

      if (this.saved.includes(input)) {
        const i = this.saved.findIndex(i => i == input);
        if (i > -1) {
          window.localStorage.removeItem(key);
          this.saved.splice(i, 1);
        }
      } else {
        window.localStorage.setItem(key, this.calculator.inputs[input].value);
        this.saved.push(input);
      }

      this.$store.commit("updateModel", key);
    },

    updateInputs(limit = 0, updatedInputs) {
      let errors = [];

      this.calculator.inputs.map((input, index) => {
        // Just update another inputs
        if (!updatedInputs.includes(index)) {
          // Use correct formula
          const formula = this.chooseCorrectFormula(input, updatedInputs);

          if (formula) {
            //  Create propositions from formula
            const proposition = this.createPropositions(formula);
            console.log("formula of " + index + " is: " + proposition);

            try {
              const newValue = new Function("return " + proposition)();
              input.value = this.normalizeNumbers(newValue);
              updatedInputs.push(index);
              this.lastUpdatedInputs.push(index);
            } catch (error) {
              console.log(error.message);
            }
          } else {
            errors.push(index);
          }
        }
      });

      if (errors.length) {
        if (limit < this.calculator.inputs.length) {
          return this.updateInputs(limit + 1, updatedInputs);
        } else {
          console.log("can't calculate input index:", errors);
          return false;
        }
      }

      return true;
    },

    chooseCorrectFormula(model, updatedInputs) {
      const possible = this.getPossibleFormulas(updatedInputs);
      const available = Object.keys(model.formulas);

      const common = this.getCommonsOf(possible, available);

      if (!common.length) {
        return false;
      }

      const key = common[0];
      return model.formulas[key].split(" ");
    },

    getPossibleFormulas(updatedInputs) {
      const possibleFormula = [...updatedInputs];

      updatedInputs.sort((a, b) => a - b);

      for (let i = 0; i < updatedInputs.length - 1; i++) {
        for (let j = i + 1; j < updatedInputs.length; j++) {
          possibleFormula.push(updatedInputs[i] + "," + updatedInputs[j]);
        }
      }

      return possibleFormula;
    },

    getCommonsOf(possibles, availables) {
      const common = [];

      for (let i = 0; i < possibles.length; i++) {
        for (let j = 0; j < availables.length; j++) {
          if (possibles[i] == availables[j]) {
            common.push(possibles[i]);
          }
        }
      }

      return common;
    },

    createPropositions(formula) {
      const propositions = [];

      formula.map((item, i) => {
        if (i % 2 == 1) {
          //  directly push operations
          propositions.push(item);
        } else if (item.includes("[") && item.includes("]")) {
          // item in formula that contains '[]' is an index of inputs value to use in formula
          const parameterIndex = item.substr(1, item.length - 2);
          propositions.push(
            "(" + this.calculator.inputs[parameterIndex].value + ")"
          );
        } else {
          // somethings like (numbers, paranthessiss, ..)
          propositions.push(item);
        }
      });

      return propositions.join("");
    },

    normalizeNumbers(number) {
      const accuracy = 100000;

      return Math.round(number * accuracy) / accuracy;
    }
  },

  computed: {
    ...mapState(["models", "currency"])
  },

  created() {
    this.refreshCalculator();
  }
};
</script>
